body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Pomodoro {
    text-align: center;
    background: -webkit-linear-gradient(#667db6, #0082c8, #0082c8, #667db6);
    background: linear-gradient(#667db6, #0082c8, #0082c8, #667db6);
    /* https://uigradients.com/#Hydrogen */
    color: white;
    min-height: 100vh;
    font-family: 'Montserrat', sans-serif;
}

.Pomodoro-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}
.Controls {
    margin: 0;
    display: flex;
    justify-content: center;
}
.Label {
    margin: 2rem;
}


/* https://www.w3schools.com/howto/howto_css_arrows.asp */

.arrow {
    border: solid white;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
}

.up {
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
}

.down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
}
.Timer {
    margin: 1rem auto
}

.time-left {
    font-size: 6rem;
    line-height: 1;
    margin: 0;
}
/* https://www.w3schools.com/howto/tryit.asp?filename=tryhow_css_shapes_triangle-right */

.triangle-right {
    margin: auto;
    background: transparent;
    color: transparent;
    width: 0;
    height: 0;
    border-top: 17px solid transparent;
    border-left: 35px solid white;
    border-right: 0px solid transparent;
    border-bottom: 17px solid transparent;
}

.pause-button {
    margin: auto;
    width: 35px;
    height: 35px;
    border-color: white;
    border-style: double;
    border-width: 0px 35px 0px 0px;
    -webkit-transform: translateX(-25%);
            transform: translateX(-25%);
}
